  background-repeat: no-repeat;
  padding: 2% !important;
  border-radius: 20px;
  max-width: 700px !important;
}

/* card */
.MuiPaper-root.MuiPaper-elevation.MuiPaper-rounded.MuiPaper-elevation1.MuiCard-root {
  margin: 1% !important;
  margin-bottom: 20px !important;
  margin-top: 20px !important;
}

.logo {
  height: auto;
  width: 40%;
  margin-top: 10px;
  padding: 30px;
  padding-top: 10px;
}

.card-wrapper {
  padding-left: 20px;
  padding-right: 20px;
}

.show-when-responsive {
  display: none;
}

.step-contents {
  width: 70%;
}

@media only screen and (max-width: 600px) {
  .MuiPaper-root.MuiPaper-elevation.MuiPaper-rounded.MuiPaper-elevation1.MuiCard-root {
    margin: 1% !important;
    margin-bottom: 20px !important;
    margin-top: 20px !important;
  }

  .logo {
    height: auto;
    width: 100%;
    margin-top: 0px;
    padding: 20px;
    padding-top: 0px;
  }

  .step-contents {
    width: 100%;
  }

  .hide-when-responsive {
    display: none !important;
  }

  .show-when-responsive {
    display: block;
  }

  .responsive-wallet-btn {
    width: 100%;
    margin-top: 10px;
    margin-bottom: 20px;
    padding-left: 10px;
    padding-right: 30px;
  }

  .green-custom-button {
    height: 40px;
    font-size: 30px !important;
    padding: 20px !important;
    background-color: #cabd97 !important;
  }
}

.navbar {
  display: flex;

  justify-content: space-between;
}
